import React, { useEffect } from 'react';
import { fade, makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import SearchIcon from '@material-ui/icons/Search';
import InputBase from '@material-ui/core/InputBase';
import MaterialLocalizationComponent from './material-localization.component';
import Divider from '@material-ui/core/Divider';
import Select from '@material-ui/core/Select';
import materialTypeService from '../services/material-type.service';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';

const useStyles = makeStyles((theme) => ({
    container: {
        height: '100%',
        width: '100%'
    },
    paper: {
        padding: theme.spacing(1),
        textAlign: 'center',
        color: theme.palette.text.secondary,
        whiteSpace: 'nowrap',
        marginBottom: theme.spacing(1),
    },
    searchIcon: {
        position: 'absolute',
        pointerEvents: 'none',
        alignItems: 'center',
        justifyContent: 'center',
    },
    inputInput: {
        padding: theme.spacing(1, 1, 1, 0),
        // vertical padding + font size from searchIcon
        paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('md')]: {
            width: '20ch',
        },
	},
	searchSelectContainer: {
		marginTop: '10px',
		width: '100%',
	},
	searchSelect: {
		textAlign: 'left',
		color: 'rgba(0,0,0,0.54)'
	},
    search: {
        display: 'flex',
        alignItems: 'center',
        position: 'relative',
        borderRadius: theme.shape.borderRadius,
        backgroundColor: fade(theme.palette.common.white, 0.15),
        '&:hover': {
            backgroundColor: fade(theme.palette.common.white, 0.25),
        },
        width: '100%',
        [theme.breakpoints.up('sm')]: {
            width: 'auto',
        }
	},
	searchDefaultMenuItem: {
		color: 'rgba(0, 0, 0, 0.38)'
	}
}));
export default function SearchComponent(props) {
    const classes = useStyles();
    const [materials, setMaterials] = React.useState(props.materials);
    const [filteredMaterials, setFilteredMaterials] = React.useState(props.materials);
	const [selectedMaterial, setSelectedMaterial] = React.useState();
	const [materialTypes, setMaterialTypes] = React.useState([]);
	const [selectedMaterialTypeUuid, setSelectedMaterialTypeUuid] = React.useState();
	const [filter, setFilter] = React.useState();

    useEffect(() => {
        setMaterials(props.materials);
    }, [props.materials]);

    useEffect(() => {
        setFilteredMaterials(materials);
	}, [materials]);
	
	useEffect(() => {
		
		//arr.unshift();
		(async () => setMaterialTypes(await materialTypeService.getList()))();
	}, []);
	
	/*useEffect(() => {
		if (materialTypes && materialTypes.length) {
			setSelectedMaterialTypeUuid(materialTypes[0].uuid);
		}
	}, [materialTypes]);*/
	
	useEffect(() => {
        applyFilter();
	}, [selectedMaterialTypeUuid]);

	useEffect(() => {
		applyFilter();
	}, [filter]);

    /**
     * Filters the list of materials.
     * 
     * @param {*} filter The filter.
     */
    const applyFilter = () => {
		let tmpFilteredMaterials = materials;
        if (filter) {
			tmpFilteredMaterials = tmpFilteredMaterials.filter(item => {
				return item.name.toLowerCase().indexOf(filter.toLowerCase()) > -1 && (!selectedMaterialTypeUuid || (item.materialType && item.materialType.uuid === selectedMaterialTypeUuid));
			});
		}
		
		tmpFilteredMaterials = tmpFilteredMaterials.filter(item => {
			return !selectedMaterialTypeUuid || (item.materialType && item.materialType.uuid === selectedMaterialTypeUuid);
		});

		setFilteredMaterials(tmpFilteredMaterials);
    }

    /**
     * On filter input change.
     * 
     * @param {*} event The event.
     */
    const onFilterChange = (event) => {
		setFilter(event.target.value);
    };

    const onMaterialSearch = (materialId) => {
        if (props.onMaterialSearch) props.onMaterialSearch(materialId);
        setSelectedMaterial(materialId);
	}
	
	const onMaterialTypeChange = (e) => {
		setSelectedMaterialTypeUuid(e.target.value);
	};

    return (
        <div className={classes.container}>
            <Paper className={classes.paper}>
                <div className={classes.search}>
                    <div className={classes.searchIcon}>
                        <SearchIcon />
                    </div>
                    <InputBase
                        onChange={onFilterChange}
                        placeholder="Rechercher"
                        classes={{
                            root: classes.inputRoot,
                            input: classes.inputInput,
                        }}
                        inputProps={{ 'aria-label': 'search' }}
                    />
                </div>
				<FormControl className={classes.searchSelectContainer}>
					<InputLabel>Type</InputLabel>
					<Select
						value={selectedMaterialTypeUuid ? selectedMaterialTypeUuid : ''} 
						onChange={onMaterialTypeChange}
						className={classes.searchSelect}
						>
						<MenuItem className={classes.searchDefaultMenuItem} key={null} value={null}>Type</MenuItem>
						{
							materialTypes.map(materialType => (
								<MenuItem key={materialType.uuid} value={materialType.uuid}>{materialType.name}</MenuItem>
							))
						}
					</Select>
				</FormControl>
                <Divider />
                <MaterialLocalizationComponent selectedMaterial={selectedMaterial} onSearch={onMaterialSearch} materials={filteredMaterials}></MaterialLocalizationComponent>
            </Paper>
        </div>
    );
}


