import React, { useEffect } from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
    root: {
        width: '300px'
    }
}));

export default function MaterialTypeFormDialogComponent(props) {
    const [open, setOpen] = React.useState(props.open);
	const [materialType, setMaterialType] = React.useState(props.materialType);
	
    useEffect(() => setOpen(props.open), [props.open]);
    useEffect(() => setMaterialType(props.materialType), [props.materialType]);

    const handleClose = () => {
        setOpen(false);
        if (props.onClose) props.onClose(materialType);
    };

    const handleCancel = () => {
        if (props.onClose) props.onClose(undefined);
    };

    const onNameChange = (e) => {
        materialType.name = e.target.value;
        setMaterialType(materialType);
    };

    const classes = useStyles();

    return (
        <div>
            <Dialog open={open} onClose={handleClose}>
                <DialogTitle id="form-dialog-title">{materialType.uuid ? 'Modification' : 'Ajout'} d'un type de matériau</DialogTitle>
                <DialogContent className={classes.root}>
                    <TextField
                        autoFocus
                        margin="dense"
                        id="name"
                        label="Nom"
                        fullWidth
                        defaultValue={materialType.name}
                        onChange={onNameChange}
                    />
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleCancel} color="primary">
                        Annuler
                    </Button>
                    <Button onClick={handleClose} color="primary">
                        {materialType.uuid ? 'Modifier' : 'Ajouter'}
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    );
}


