import React, { useEffect } from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import { makeStyles } from '@material-ui/core/styles';
import Select from '@material-ui/core/Select';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';


import materialTypeService from '../services/material-type.service';

const useStyles = makeStyles((theme) => ({
    root: {
        width: '300px'
    }, formControl: {
		width: '300px'
	  },
	  selectEmpty: {
		marginTop: theme.spacing(2),
	  }
}));

export default function MaterialFormDialogComponent(props) {
    const [open, setOpen] = React.useState(props.open);
	const [material, setMaterial] = React.useState(props.material);
	const [materialTypes, setMaterialTypes] = React.useState([]);

    useEffect(() => setOpen(props.open), [props.open]);
	useEffect(() => setMaterial(props.material), [props.material]);
	
	useEffect(() => {
        (async () => setMaterialTypes(await materialTypeService.getList()))();
    }, []);

    const handleClose = () => {
        setOpen(false);
        if (props.onClose) props.onClose(material);
    };

    const handleCancel = () => {
        if (props.onClose) props.onClose(undefined);
    };

    const onNameChange = (e) => {
        material.name = e.target.value;
        setMaterial(material);
	};
	
	const onMaterialTypeChange = (e) => {
		if (!material.materialType) {
			material.materialType = {};
		}
		material.materialType.uuid = e.target.value;
		const updatedMaterial = Object.assign({}, material);
		setMaterial(updatedMaterial);
	};

    const classes = useStyles();

    return (
        <div>
            <Dialog open={open} onClose={handleClose}>
                <DialogTitle id="form-dialog-title">{material.uuid ? 'Modification' : 'Ajout'} d'un matériau</DialogTitle>
                <DialogContent className={classes.root}>
                    <TextField
                        autoFocus
                        margin="dense"
                        id="name"
                        label="Nom"
                        fullWidth
                        defaultValue={material.name}
                        onChange={onNameChange}
                    />

					<FormControl className={classes.formControl}>
						<InputLabel>Type</InputLabel>
						<Select
							value={material.materialType ? material.materialType.uuid : undefined}
							onChange={onMaterialTypeChange}
							>
							<MenuItem key={null} value={""}>Type</MenuItem>
							{
								materialTypes.map(materialType => (
									<MenuItem key={materialType.uuid} value={materialType.uuid}>{materialType.name}</MenuItem>
								))
							}
						</Select>
					</FormControl>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleCancel} color="primary">
                        Annuler
                    </Button>
                    <Button onClick={handleClose} color="primary">
                        {material.uuid ? 'Modifier' : 'Ajouter'}
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    );
}


