import React, { useEffect } from 'react';
import materialTypeService from '../services/material-type.service';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';

import { makeStyles } from '@material-ui/core/styles';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';

import IconButton from '@material-ui/core/IconButton';

import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';

import MaterialTypeFormDialogComponent from './materialtype-form-dialog.component';
import ConfirmationDialogComponent from './confirmation-dialog.component';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        width: '100%',
        backgroundColor: theme.palette.background.paper,
    },
    actionIcons: {
        textAlign: 'right'
    },
    listitem: {
        paddingLeft: '32px'
    },
    fab: {
        position: 'absolute',
        bottom: theme.spacing(2),
        right: theme.spacing(2),
      }
}));

export default function MaterialTypesComponent() {
    const [dialogOpen, setDialogOpen] = React.useState(false);
    const [deleteConfirmationOpen, setDeleteConfirmationOpen] = React.useState(false);
    const [materialTypes, setMaterialTypes] = React.useState([]);
    const [materialTypeToEdit, setMaterialTypeToEdit] = React.useState({name: ''});
    const [materialTypeToDelete, setMaterialTypeToDelete] = React.useState(undefined);

    const openDialog = () => {
        setDialogOpen(true)
    };

    const onDialogClose = (materialType) => {
        setDialogOpen(false);
        if (materialType) {
            if (!materialType.uuid) {
                createMaterialType(materialType);
            } else {
                updateMaterialType(materialType);
            }
        }
    };

    const openDeleteMaterialTypeDialog = (materialType) => {
        setDeleteConfirmationOpen(true);
        setMaterialTypeToDelete(materialType);
    };

    const createMaterialType = async (materialType) => {
        const createdMaterialType = await materialTypeService.create(materialType);
        setMaterialTypes([...materialTypes, createdMaterialType])
    };

    const deleteMaterialType = async (materialType) => {
        await materialTypeService.delete(materialType);
        setMaterialTypes([...materialTypes.filter(m => m.uuid !== materialType.uuid)]);
    };

    const updateMaterialType = async (materialType) => {
        await materialTypeService.update(materialType);
        materialTypes.find(m => m.uuid === materialType.uuid).name = materialType.name;
        setMaterialTypes([...materialTypes]);

    };

    const editMaterialType = (materialType) => {
        setMaterialTypeToEdit({
            uuid: materialType.uuid,
            name: materialType.name
        });
        setDialogOpen(true);
    };

    const onFabClick = () => {
        setMaterialTypeToEdit({name: ''});
        openDialog();
    };

    const onAgree = async () => {
        await deleteMaterialType(materialTypeToDelete);
        setDeleteConfirmationOpen(false);
    };

    const onDisagree = () => {
        setDeleteConfirmationOpen(false);
    };

    const getMaterialTypesSorted = () => {
        return materialTypes.sort((a, b) => {
            if(a.name < b.name) { return -1; }
            if(a.name > b.name) { return 1; }
            return 0;
        });
    }

    /* Load materials. */
    useEffect(() => {
        (async () => setMaterialTypes(await materialTypeService.getList()))();
    }, []);

    const classes = useStyles();
    return (
        <div className={classes.root}>
            <MaterialTypeFormDialogComponent open={dialogOpen} onClose={onDialogClose} materialType={materialTypeToEdit} />
            <ConfirmationDialogComponent open={deleteConfirmationOpen} agree={onAgree} disagree={onDisagree} />
                <List component="nav">
                    {
                        getMaterialTypesSorted().map(materialType => (
                            <ListItem className={classes.listitem} key={materialType.key}>
                                    <ListItemText className={"noselect"} primary={materialType.name} />
                                <div className={classes.actionIcons}>

                                    <IconButton edge="start" onClick={() => editMaterialType(materialType)}>
                                        <EditIcon />
                                    </IconButton>
                                    <IconButton edge="start" onClick={() => openDeleteMaterialTypeDialog(materialType)}>
                                        <DeleteIcon />
                                    </IconButton>
                                </div>
                            </ListItem>
                        ))
                    }
                </List>
            <Fab className={classes.fab} color="primary" onClick={onFabClick}>
                <AddIcon />
            </Fab>
        </div>
    );
}


