import React, { Component } from 'react';
import './App.css';

import AppBar from './functions/appbar.function';
import InteractiveMap from './functions/interactive-map.function';
import MaterialsComponent from './components/materials.component';
import MaterialTypesComponent from './components/materialtypes.component';
import ZonesComponent from './components/zones.component';

export default function App(props) {
  const [routing, setRouting] = React.useState('material-localizer');

  let routingComponent;
  if (routing === 'material-localizer') {
    routingComponent = <InteractiveMap />;
  } else if (routing === 'materials-management') {
    routingComponent = <MaterialsComponent />;
  } else if (routing === 'materialtypes-management') {
	routingComponent = <MaterialTypesComponent />;
  } else if (routing === 'zone-management') {
    routingComponent = <ZonesComponent />;
  }


const menuClick = (menuText) => {
  if (menuText === 'Gérer les matériaux') menuText = 'materials-management';
  if (menuText === 'Gérer les types de matériaux') menuText = 'materialtypes-management';
  if (menuText === 'Localiser') menuText = 'material-localizer';
  if (menuText === 'Gérer les zones') menuText = 'zone-management';
  setRouting(menuText);
};

  return (

    <div className="App">
      <AppBar onMenuClick={menuClick} />
      {routingComponent}
    </div>
  );
}


