import {environment} from '../environment/environment';

module.exports = {
    getList: async function() {
        const response = await fetch(`${environment.apiAddress}/api/v1/tecniplast/materials/types`);
        const json = await response.json();

        return json.response.map(item => {
            return {
              key: item.id,
              name: item.name,
              uuid: item.uuid
            };
        });
    },

    create: async function(materialType) {
        const requestOptions = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                name: materialType.name
            })
        };
        const response = await fetch(`${environment.apiAddress}/api/v1/tecniplast/materials/types`, requestOptions);
        const json = await response.json();

        return json.response.map(item => {
            return {
              key: item.id,
              name: item.name,
              uuid: item.uuid
            };
        })[0];
    },

    delete: async function(materialType) {
        const requestOptions = {
            method: 'DELETE',
            headers: { 'Content-Type': 'application/json' }
        };

        const response = await fetch(`${environment.apiAddress}/api/v1/tecniplast/materials/types/${materialType.uuid}`, requestOptions);
        const json = await response.json();

        return json;
    },

    update: async function(materialType) {
        const requestOptions = {
            method: 'PUT',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                name: materialType.name
            })
        };
        const response = await fetch(`${environment.apiAddress}/api/v1/tecniplast/materials/types/${materialType.uuid}`, requestOptions);
        const json = await response.json();

        return json.response.map(item => {
            return {
              key: item.id,
              name: item.name,
              uuid: item.uuid
            };
        });
    }
}


